import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode :'history',
  routes: [
    {
      path: '/home/:type',
      name: 'home',
      component: () => import('@/views/home/home'),
      // redirect:'/home/manage',
      redirect:to=>{
        console.log('******'+typeof to.params.type);
        console.log('******'+ to.params.type);
        // 这里可以做权限盘点然后向指定的页面跳转
        return `/home/&{to.params.type}`;
      },

      children:[
        {
          path: '/home/manage',
          name: 'manage',
          component: () => import('@/views/home/manage'),
        },
        {
          path: '/home/user',
          name: 'user',
          component: () => import('@/views/home/user'),
        }
      ]
    },
    {
      path: '/',
      name: 'login',
      component: () => import('@/views/login/login'),

    }
  ]
})



